html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Medium";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

body {
  padding: 0;
  height: 100%; }

.top-wrapper {
  margin-left: auto;
  margin-right: auto;
  max-width: 1920px;
  padding-top: 33px;
  background: url("/img/header_background.png") center top no-repeat, url("/img/group_of_two_reactangles.png") center top no-repeat;
  background-size: cover, auto;
  background-color: #1b2d37; }

header {
  display: flex;
  flex-direction: row;
  max-width: 1411px;
  margin-left: 13.17708333333333%; }

.logo {
  cursor: pointer; }

.logo:hover {
  opacity: 0.5; }

.name {
  margin-left: 1.13394755492558%;
  cursor: pointer; }
  .name-firm {
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-size: 25px;
    line-height: 29px;
    color: #ffffff;
    margin: 0;
    margin-top: 6px; }
  .name-tagline {
    font-family: "Roboto Medium", "Arial", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-size: 12px;
    line-height: 14px;
    color: #788891;
    margin: 0; }

.name:hover {
  opacity: 0.5; }

.mailto,
.phone-number {
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 18px;
  margin-left: 39.12119064493267%;
  text-decoration: none;
  color: #ffffff;
  cursor: pointer; }

.mailto {
  margin-left: 39.12119064493267%; }

.mailto span {
  margin-left: 20px; }

.phone-number {
  margin-left: 6.23671155209072%; }

.phone-number span {
  margin-left: 16px; }

.mailto:hover,
.phone-number:hover {
  opacity: 0.5; }

header button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  align-self: center;
  margin-left: auto;
  width: 180px;
  height: 45px;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer; }

header button:hover {
  opacity: 0.5; }
